.pressrelease {
  padding: 100px 0;
  &__desc {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    h3 {
      font-weight: 600;
      font-size: 22px;
      line-height: 120%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #0060A9;
      padding-left: 25px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%) rotate(-45deg);
        width: 10px;
        height: 10px;
        background: #E18A04;
        border-radius: 2px;
      }
    }
    p {
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #4F4F4F;
      padding-left: 25px;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__desc2 {
    margin-bottom: 30px;
    p {
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #4F4F4F;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .pressrelease {
    padding: 50px 0;
    &__desc {
      h3 {
        font-size: 20px;
      }
      p {
        font-size: 16px;
      }
    }
    &__desc2 {
      p {
        font-size: 16px;
      }
    }
  }
}