.main-service {
  background-image: url("../img/main-service.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 180px 0;
  &__title {
    display: inline-block;
    font-weight: 700;
    font-size: 45px;
    line-height: 120%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #0060A9;
    border-bottom: 1px solid #0060A9;
    padding-bottom: 15px;
  }
  &__desc {
    font-size: 17px;
    line-height: 140%;
    color: #0060A9;
    margin-top: 15px;
    max-width: 606px;
  }
  &__btn {
    width: 191px;
    height: 50px;
    margin-top: 15px;
  }
}
.info-service {
  padding: 100px 0;
  &__desc {
    p {
      font-weight: 700;
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #4F4F4F;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
}
.info2-service {
  padding: 100px 0;
  background: #F3F7FD;
  &__desc {
    p {
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #4F4F4F;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__title {
    font-weight: 600;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #0060A9;
    margin-top: 35px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 32px;
    margin-top: 25px;
  }
  &__item {
    &-number {
      font-weight: 700;
      font-size: 55px;
      line-height: 90%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #0060A9;
    }
    &-desc {
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #4F4F4F;
      margin-top: 15px;
    }
  }
}
.info3-service {
  padding: 100px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    max-width: 415px;
    margin-right: 32px;
  }
  &__right {
    max-width: calc(100% - 447px);
  }
  &__title {
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #0060A9;
  }
  &__list {
    margin-top: 10px;
    li {
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #4F4F4F;
      padding-left: 10px;
      position: relative;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 13px;
        left: 0;
        transform: translate(0, -50%) rotate(-45deg);
        width: 4px;
        height: 4px;
        background: #E18A04;
        border-radius: 1px;
      }
    }
  }
  &__desc {
   p {
     font-size: 18px;
     line-height: 140%;
     letter-spacing: 0.02em;
     color: #4F4F4F;
     &:not(:first-child) {
       margin-top: 15px;
     }
   }
  }
}

@media screen and (max-width: 992px) {
  .main-service {
    padding: 130px 0;
    &__title {
      font-size: 38px;
    }
    &__desc {
      font-size: 16px;
    }
  }
  .info-service {
    padding: 50px 0;
    &__desc {
      p {
        font-size: 16px;
      }
    }
  }
  .info2-service {
    padding: 50px 0;
    &__desc {
      p {
        font-size: 16px;
      }
    }
    &__title {
      font-size: 20px;
    }
    &__items {
      grid-template-columns: 1fr 1fr;
    }
    &__item {
      &-number {
        font-size: 36px;
      }
      &-desc {
        font-size: 16px;
      }
    }
  }
  .info3-service {
    padding: 50px 0;
    &__inner {
      display: block;
    }
    &__left {
      max-width: 100%;
      margin: 0 0 30px 0;
    }
    &__right {
      max-width: 100%;
    }
    &__title {
      font-size: 16px;
    }
    &__list {
      li {
        font-size: 16px;
        &:after {
          top: 12px;
        }
      }
    }
    &__desc {
      font-size: 16px;
    }
  }
}

@media screen and (max-width: 768px) {
  .main-service {
    padding: 80px 0;
    &__title {
      font-size: 36px;
    }
  }
  .info2-service {
    &__items {
      grid-template-columns: 1fr;
    }
  }
}