.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 180px 0;
  &__title {
    display: inline-block;
    font-weight: 700;
    font-size: 45px;
    line-height: 120%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #0060A9;
    border-bottom: 1px solid #0060A9;
    padding-bottom: 15px;
  }
  &__desc {
    font-size: 17px;
    line-height: 140%;
    color: #0060A9;
    margin-top: 15px;
    max-width: 606px;
  }
  &__btn {
    width: 191px;
    height: 50px;
    margin-top: 15px;
  }
}
.info {
  padding: 100px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    max-width: 470px;
    margin-right: 30px;
  }
  &__right {
    max-width: calc(100% - 500px);
  }
  &__title {
    span {
      text-transform: uppercase;
    }
  }
  &__desc {
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #4F4F4F;
  }
  &__link {
    font-size: 18px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #E18A04;
    display: block;
    margin-top: 15px;
    &:hover {
      color: #C97A03;
    }
  }
}
.services {
  padding: 100px 0;
  background: #F3F7FD;
  &2 {
    background: #ffffff;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: 1fr;
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    position: relative;
    &--active {
      z-index: 3;
      .services__item-show {
        border: 1px solid #ffffff;
        border-bottom: none;
        border-radius: 35px 35px 0 0;
      }
      .services__item-show2 {
        width: calc(200% + 32px);
      }
      .services__item-hidden2 {
        width: calc(200% + 32px);
      }
      .show-bg {
        background: #F3F7FD;
      }
      .hidden-bg {
        background: #F3F7FD;
      }
    }
    &-show {
      background: #FFFFFF;
      border: 1px solid #E18A04;
      border-radius: 35px;
      padding: 50px 30px 40px;
      height: 100%;
    }
    &-hidden {
      width: 100%;
      display: none;
      position: absolute;
      z-index: 2;
      top: calc(100% - 30px);
      left: 0;
      background: #FFFFFF;
      padding: 0 30px 30px;
      border-radius: 0 0 35px 35px;
      border: 1px solid #ffffff;
      border-top: unset;
      &--active {
        display: block;
      }
      p {
        font-size: 18px;
        line-height: 140%;
        letter-spacing: .02em;
        color: #4f4f4f;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
      ul {
        margin-top: 5px;
        li {
          font-size: 18px;
          line-height: 140%;
          color: #4F4F4F;
          padding-left: 15px;
          position: relative;
          &:after {
            content: '';
            position: absolute;
            top: 11px;
            left: 0;
            width: 3px;
            height: 3px;
            background: #4F4F4F;
          }
        }
      }
    }
    &-img {
      max-width: 80px;
      img {
        width: 100%;
      }
    }
    &-title {
      font-weight: 600;
      font-size: 22px;
      line-height: 120%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #4F4F4F;
      margin-top: 35px;
    }
  }
}
.organization {
  padding: 100px 0;
  background: #F3F7FD;
  &__title {
    text-align: center;
  }
  &__slider {
    position: relative;
    margin-top: 30px;
  }
  &-slide {
    height: 90px;
    background: #FFFFFF;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    img {
      max-width: 141px;
      width: 100%;
    }
  }
  &-button__prev {
    left: -67px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    border: 1px solid #BDBDBD;
    background: transparent;
    &:after {
      display: none;
    }
    &:hover {
      border: 1px solid #E18A04;
      svg path {
        fill: #E18A04;
        stroke: #E18A04;
      }
    }
  }
  &-button__next {
    right: -67px;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    border: 1px solid #4F4F4F;
    background: transparent;
    svg {
      transform: rotate(180deg);
    }
    &:after {
      display: none;
    }
    &:hover {
      border: 1px solid #E18A04;
      svg path {
        fill: #E18A04;
        stroke: #E18A04;
      }
    }
  }
}
.info2 {
  padding: 100px 0;
  &__title {
    text-align: center;
  }
  &__items {
    margin-top: 30px;
  }
  &__item {
    border: 1px solid #E18A04;
    border-radius: 20px;
    &:not(:first-child) {
      margin-top: 35px;
    }
    &--active {
      background: #F3F7FD;
      border: 1px solid transparent;
      .info2__item-title {
        color: #4F4F4F;
      }
      .info2__item-btn {
        background: #E18A04;
        svg {
          transform: rotate(180deg);
          path {
            fill: #ffffff;
          }
        }
      }
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      cursor: pointer;
      padding: 30px;
    }
    &-hidden {
      padding: 0 30px 30px 30px;
      display: none;
    }
    &-title {
      font-weight: 600;
      font-size: 22px;
      line-height: 120%;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #E18A04;
    }
    &-btn {
      width: 35px;
      height: 35px;
      border: 1px solid #E18A04;
      border-radius: 50%;
      background: transparent;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      svg {
        transition: all 0.5s;
      }
    }
    &-desc {
      p {
        font-size: 18px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #4F4F4F;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &-subtitle {
      font-size: 18px;
      line-height: 140%;
      letter-spacing: 0.02em;
      color: #0060A9;
    }
    &-info {
      &:not(:last-child) {
        margin-bottom: 15px;
      }
      a {
        font-size: 18px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #E18A04;
      }
    }
    &-mail {
      color: #E18A04;
      text-decoration: underline;
    }
  }
}

@media screen and (max-width: 1600px) {
  .organization {
    &-slider__btns {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-top: 60px;
    }
    &-button__prev {
      position: static;
      order: -1;
    }
    &-button__next {
      position: static;
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .services {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
    &__item {
      &--active {
        .services__item-show2 {
          width: 100%;
        }
        .services__item-hidden2 {
          width: 100%;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 130px 0;
    &__title {
      font-size: 38px;
    }
    &__desc {
      font-size: 16px;
    }
  }
  .info {
    padding: 50px 0;
    &__inner {
      display: block;
    }
    &__left {
      max-width: 100%;
      margin: 0 0 15px 0;
    }
    &__right {
      max-width: 100%;
    }
    &__title {
      br {
        display: none;
      }
    }
    &__desc {
      font-size: 16px;
    }
    &__link {
      font-size: 16px;
    }
  }
  .services {
    padding: 50px 0;
    &__item {
      &--active {
        .services__item-show {
          border: 1px solid #E18A04;
          border-radius: 35px;
        }
        .services__item-hidden {
          display: none;
        }
        .show-bg {
          background: transparent;
        }
        .hidden-bg {
          background: transparent;
        }
      }
      &-img {
        max-width: 50px;
      }
      &-title {
        font-size: 18px;
      }
    }
  }
  .organization {
    padding: 50px 0;
  }
  .info2 {
    padding: 50px 0;
    &__item {
      &:not(:first-child) {
        margin-top: 20px;
      }
      &-show {
        padding: 20px 15px;
      }
      &-hidden {
        padding: 0 15px 20px;
      }
      &-title {
        font-size: 20px;
      }
      &-btn {
        width: 30px;
        height: 30px;
      }
      &-desc {
        p {
          font-size: 16px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    padding: 80px 0;
    &__title {
      font-size: 36px;
    }
  }
  .services {
    &__items {
      gap: 20px;
    }
    &__item {
      &-show {
        padding: 20px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .services {
    &__items {
      gap: 15px;
    }
    &__item {
      &-show {
        padding: 15px;
      }
      &-title {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .services {
    &__items {
      grid-template-columns: 1fr;
    }
  }
}