.footer {
  background: #0060A9;
  padding: 100px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    display: flex;
  }
  &__right {
    margin-left: 30px;
  }
  &__logo {
    display: block;
    width: 143px;
    img {
      width: 100%;
    }
  }
  &__rights {
    font-size: 12px;
    line-height: 120%;
    color: #BDBDBD;
    margin-top: 15px;
  }
  &__connection {
    margin-left: 150px;
  }
  &__info {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 18px;
    }
    i {
      max-width: 24px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.02em;
      color: #FFFFFF;
      margin-left: 15px;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
  }
  &__social {
    max-width: 35px;
    &:not(:first-child) {
      margin-left: 25px;
    }
    img {
      width: 100%;
    }
  }
  &__link {
    display: block;
    font-size: 12px;
    line-height: 120%;
    color: #E18A04;
    margin-top: 60px;
    &:hover {
      color: #C97A03;
    }
  }
}

@media screen and (max-width: 992px) {
  .footer {
    padding: 50px 0;
    &__connection {
      margin-left: 35px;
    }
    &__info {
      i {
        max-width: 18px;
      }
    }
    &__social {
      max-width: 25px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__inner {
      display: block;
    }
    &__right {
      margin: 30px 0 0 0;
    }
    &__left {
      display: block;
    }
    &__connection {
      margin: 30px 0 0 0;
    }
    &__link {
      margin-top: 30px;
    }
  }
}