.header {
  background: #0060A9;
  padding: 25px 0;
  .container {
    max-width: 1500px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__logo {
    display: flex;
    align-items: center;
    img:nth-child(1) {
      width: 50px;
    }
    img:nth-child(2) {
      width: 115px;
      margin-left: 15px;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li:not(:first-child) {
      margin-left: 25px;
    }
  }
  &__link {
    font-size: 18px;
    line-height: 120%;
    color: #FFFFFF;
    &:hover {
      color: #E18A04;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
      .header__item-show {
        span {
          color: #E18A04;
        }
      }
    }
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      transition: all 0.3s;
      &:hover {
        span {
          color: #E18A04;
        }
      }
      span {
        font-size: 18px;
        line-height: 120%;
        color: #FFFFFF;
        transition: all 0.5s;
      }
      img {
        width: 14px;
        margin-left: 5px;
        display: block;
        flex-shrink: 0;
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      min-width: 230px;
      width: 100%;
      background: #0060A9;
      padding: 15px;
      opacity: 0;
      pointer-events: none;
    }
    &-link {
      font-size: 18px;
      line-height: 120%;
      color: #FFFFFF;
      display: block;
      padding: 2px;
      &:hover {
        color: #E18A04;
      }
      &:not(:first-child) {
        margin-top: 12px;
      }
    }
  }
  &__burger {
    width: 35px;
    height: 20px;
    position: relative;
    z-index: 10;
    cursor: pointer;
    display: none;
    span {
      width: 100%;
      height: 2px;
      position: absolute;
      left: 0;
      background: #ffffff;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 9px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 9px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__logo {
      img:nth-child(1) {
        width: 40px;
      }
      img:nth-child(2) {
        width: 100px;
      }
    }
    &__list {
      li:not(:first-child) {
        margin-left: 20px;
      }
    }
    &__link {
      font-size: 15.5px;
    }
    &__item {
      &-show {
        span {
          font-size: 15.5px;
        }
      }
      &-link {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__right {
      position: fixed;
      z-index: 7;
      top: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      opacity: 0;
      pointer-events: none;
      transition: all 0.5s;
      &--active {
        opacity: 1;
        pointer-events: all;
        .header__list {
          margin-left: 0;
        }
      }
    }
    &__list {
      width: 400px;
      height: 100%;
      background: #0060A9;
      flex-direction: column;
      align-items: start;
      padding: 100px 15px 30px;
      transition: all 0.5s;
      margin-left: -105%;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: #0060A9;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #ffffff;
        border-radius: 3px;
      }
      li:not(:first-child) {
        margin: 12px 0 0 0;
      }
      li {
        width: 100%;
      }
    }
    &__item {
      width: 100%;
      &--active {
        .header__item-hidden {
          display: block;
        }
      }
      &-hidden {
        position: static;
        display: none;
        opacity: 1;
        pointer-events: all;
        min-width: unset;
        padding: 15px 0 15px 15px;
      }
    }
    &__burger {
      display: block;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__list {
      width: 100%;
    }
  }
}