.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-orange {
  background: #E18A04;
  border-radius: 15px;
  font-size: 18px;
  line-height: 120%;
  color: #FFFFFF;
  span {
    display: flex;
    align-items: center;
    margin-left: 10px;
  }
  &:hover {
    background: #C97A03;
  }
}
.display2 {
  font-weight: 600;
  font-size: 32px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #0060A9;
}
.nav {
  background: #0060A9;
  padding: 50px 0;
  height: 100%;
  &__list {
    li {
      font-size: 20px;
      line-height: 120%;
      color: #ffffff;
      &:hover {
        color: #E18A04;
      }
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .btn-orange {
    font-size: 16px;
    span svg {
      width: 10px;
      height: 17px;
    }
  }
  .display2 {
    font-size: 28px;
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    font-size: 24px;
  }
}